/* Home 페이지 스타일 */
.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f4f4f9;
    font-family: Arial, sans-serif;
    position: relative;
  }
  
  .home-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #ffffff; /* 텍스트 색상 고정 */
    position: relative;
    z-index: 1; /* 텍스트를 박스 위로 설정 */
    margin-bottom: 1rem;
    padding: 0.5rem 1.5rem; /* 텍스트와 박스 간격 조정 */
    display: inline-block;
  }
  
  .home-title::before {
    content: "";
    position: absolute;
    top: 50%; /* 텍스트의 중간에 맞추기 */
    left: 50%;
    transform: translate(-50%, -50%) scale(1.2); /* 중심 맞추기 및 크기 조정 */
    width: 100%; /* 텍스트에 맞는 박스 크기 */
    height: 100%;
    background-color: #007bff; /* 박스 색상 */
    z-index: -1; /* 박스를 텍스트 뒤로 설정 */
    border-radius: 10px; /* 부드러운 모서리 */
  }
  
  .home-description {
    font-size: 1.2rem;
    color: #555;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .home-card {
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 2rem;
    width: 90%;
    max-width: 600px;
    text-align: center;
  }
  
  .home-input {
    width: 100%;
    padding: 10px;
    margin-bottom: 1rem;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
    transition: border-color 0.3s;
  }
  
  .home-input:focus {
    border-color: #007bff;
    outline: none;
  }
  
  .home-button {
    padding: 10px 20px;
    margin: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .home-button:hover {
    background-color: #0056b3;
  }
  
  .home-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  