/* 컨테이너 스타일 */
.sttdemo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f8f9fa;
  font-family: Arial, sans-serif;
  position: relative;
}

.sttdemo-title {
  font-size: 2rem;
  color: #ffffff; /* 텍스트 색상 고정 */
  position: relative;
  z-index: 1; /* 텍스트를 박스 위로 설정 */
  margin-bottom: 1rem;
  padding: 0.5rem 1.5rem; /* 텍스트와 박스 간격 조정 */
  display: inline-block;
}

.sttdemo-title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.2); /* 중심 맞추기 및 크기 조정 */
  width: 100%; /* 텍스트에 맞는 박스 크기 */
  height: 100%;
  background-color: #007bff; /* 박스 색상 */
  z-index: -1; /* 박스를 텍스트 뒤로 설정 */
  border-radius: 10px; /* 부드러운 모서리 */
  pointer-events: none; /* 박스가 텍스트와 상호작용하지 않도록 */
}

/* 카드 스타일 */
.sttdemo-card {
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 600px;
  text-align: center;
}

.sttdemo-script {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  padding: 10px;
  border: 2px dashed #007bff;
  border-radius: 5px;
  background-color: #eef7ff;
}

.sttdemo-result {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  color: #555;
}

/* 녹음 시작 버튼 (초록색) */
.sttdemo-button.start {
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin: 0.5rem;
}

/* 녹음 중지 버튼 (붉은색) */
.sttdemo-button.stop {
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin: 0.5rem;
}

/* WAV 파일 저장 버튼 (파란색) */
.sttdemo-button.save {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin: 0.5rem;
}

/* 전송하기 버튼 (파란색) */
.sttdemo-button.send {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin: 0.5rem;
}

/* 비활성 버튼 스타일 */
.sttdemo-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* 오류 메시지 */
.sttdemo-error {
  margin-top: 1rem;
  color: red;
  font-size: 1rem;
}
